.air-datepicker-global-container {
  z-index: 999; }

.container-help {
  box-sizing: border-box;
  width: 981px;
  max-width: 95%;
  max-width: calc(100% - 30px * 2);
  margin: 0 auto;
  line-height: 1.2; }

@media screen and (max-width: 550px) {
  .container-help {
    max-width: calc(100% - 15px * 2); } }

