.how-help {
  display: flex;
  align-items: center;
  flex-direction: column;
  margin-top: 40px;
  font-family: Arial, Helvetica, sans-serif; }

.h1 {
  font-weight: bold;
  color: #6dc3e9;
  text-transform: uppercase;
  text-align: center;
  margin: 0;
  font-size: 40px; }

.tabs-triggers {
  margin-top: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  margin-left: -30px; }
  .tabs-triggers__item {
    cursor: pointer;
    font-size: 16px;
    color: #6dc3e9;
    transition: all .3s;
    margin-left: 30px;
    margin-bottom: 30px; }
    .tabs-triggers__item--active {
      color: #f78805;
      pointer-events: none; }
    .tabs-triggers__item:hover {
      color: #f78805; }

.tabs-content {
  width: 100%; }
  .tabs-content__item {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 100%;
    display: none; }
    .tabs-content__item--active {
      display: block; }

.widget-wrap {
  position: relative;
  width: 100%; }
  .widget-wrap__bg {
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0px;
    top: 0px;
    z-index: 1;
    object-fit: cover;
    object-position: center center; }
  .widget-wrap__content {
    position: relative;
    padding: 30px 30px;
    z-index: 2; }
  .widget-wrap-oferta {
    color: #ffffff; }
    .widget-wrap-oferta a {
      font-weight: bold; }

.donations-accounts {
  margin-top: 25px; }
  .donations-accounts__desc {
    margin-top: 15px; }
  .donations-accounts__cards {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    margin-left: -30px;
    margin-bottom: -30px; }
  .donations-accounts__card {
    width: calc(50% - 30px);
    margin-left: 30px;
    margin-bottom: 30px;
    border-radius: 10px;
    border: 2px solid #6dc3e9;
    padding: 30px; }
    .donations-accounts__card-info {
      margin-top: 20px; }
    .donations-accounts__card-img {
      position: relative;
      display: block;
      height: 42px; }
      .donations-accounts__card-img::before {
        content: "";
        display: block;
        padding-top: 21%; }
      .donations-accounts__card-img img {
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        object-fit: contain;
        object-position: center center; }

.donations-row:not(:first-child) {
  margin-top: 20px; }

.donations-accounts__card-link {
  font-weight: bold;
  color: #1972ba;
  margin-top: 15px;
  display: block;
  transition-duration: .3s; }
  .donations-accounts__card-link:hover {
    text-decoration: underline; }

.online-donation {
  margin-top: 25px; }
  .online-donation__items {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    margin-left: -30px;
    margin-bottom: -30px;
    margin-top: 30px; }
  .online-donation__item {
    border-radius: 10px;
    border: 2px solid #6dc3e9;
    width: calc(33.33333% - 30px);
    margin-left: 30px;
    margin-bottom: 30px;
    padding: 15px;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all .3s; }

.things-help {
  padding: 43px;
  background-color: #6dc3e9;
  color: #fff; }
  .things-help__row:not(:first-child) {
    margin-top: 15px; }

.things-necessary {
  margin-top: 30px; }
  .things-necessary-categories {
    margin-top: 30px;
    border-radius: 10px;
    border: 2px solid #6dc3e9;
    padding: 30px;
    margin-bottom: 30px; }
  .things-necessary-category {
    margin-bottom: 30px; }
    .things-necessary-category:not(:first-child) {
      margin-top: 15px; }
    .things-necessary-category__name {
      font-weight: bold;
      color: #6dc3e9;
      text-transform: uppercase;
      text-align: center;
      font-size: 23px; }
    .things-necessary-category__desc {
      margin-top: 15px; }

.questionnaire-volunteer {
  background-color: #6dc3e9;
  display: flex;
  justify-content: center;
  padding-top: 15px;
  padding-bottom: 15px; }
  .questionnaire-volunteer-main {
    background-color: #fff;
    width: 60%;
    padding: 15px 31px; }

.questionnaire-name {
  font-size: 24px;
  color: #000;
  padding-bottom: 10px;
  border-bottom: 1px solid rgba(82, 92, 105, 0.11);
  margin-bottom: 30px;
  text-align: center; }

.questionnaire-input {
  position: relative; }

.q-input-wrap {
  position: relative; }
  .q-input-wrap + * {
    margin-top: 35px; }
  .q-input-wrap--label-top .q-input-label {
    top: -18px;
    font-size: 13px; }

.select2-container--open ~ .q-input-label,
.js-simple-select:not(.simple-select--placeholder-selected) ~ .q-input-label {
  top: -18px;
  font-size: 13px; }

.q-input {
  width: 100%;
  border: none;
  padding-bottom: 10px !important;
  padding-left: 10px !important;
  background: transparent;
  z-index: 999;
  position: relative;
  border-style: none !important;
  border-bottom: 2px solid rgba(82, 92, 105, 0.11) !important;
  border-radius: 0% !important;
  outline: none !important; }
  .q-input:focus {
    border-bottom: 2px solid #DE845A !important;
    outline: none !important; }

.q-input-label {
  display: inline-block;
  transition: all 0.5s;
  left: 10px;
  font-size: 14px;
  color: #2c131fdf;
  top: 4px;
  position: absolute;
  pointer-events: none; }
  .q-input-label span {
    color: red; }

.questionnaire-info {
  margin-top: 25px; }

.gratitude {
  padding-top: 30px;
  padding-bottom: 30px;
  text-align: center; }

.questionnaire-btn-wrap {
  margin-top: 15px;
  display: flex;
  justify-content: center;
  text-align: center; }
  .questionnaire-btn-wrap .questionnaire-btn {
    width: 100%;
    padding: 15px 20px;
    border-radius: 4px;
    cursor: pointer;
    background-color: #89C8FF;
    transition: all .3s; }
    .questionnaire-btn-wrap .questionnaire-btn:hover {
      color: #DE845A;
      background-color: blanchedalmond; }

.select2-container--custom-theme {
  transition: opacity .3s; }
  .select2-container--custom-theme.select2-container--open {
    opacity: 1; }

.simple-select-items-wrapper {
  opacity: 0;
  transition: opacity .5s;
  position: relative;
  z-index: 99888; }
  .simple-select-items-wrapper--show {
    opacity: 1; }

.select2-container--custom-theme {
  display: block;
  font-size: 14px;
  color: #2c131fdf; }
  .select2-container--custom-theme .select2-selection {
    height: 35px;
    display: flex;
    align-items: center;
    border-bottom: 2px solid rgba(82, 92, 105, 0.11); }
    .select2-container--custom-theme .select2-selection--single .select2-selection__rendered {
      width: 100%;
      padding: 5px 10px 5px 10px; }
      .select2-container--custom-theme .select2-selection--single .select2-selection__rendered::after {
        content: '';
        display: block;
        position: absolute;
        right: 10px;
        top: 13px;
        width: 16px;
        height: 11px;
        background: url(../images/select-arrow.svg) no-repeat center center;
        background-size: contain; }
  .select2-container--custom-theme .select2-selection__placeholder {
    color: #000000;
    z-index: 1222222222222222; }
  .select2-container--custom-theme .select2-dropdown {
    background: #FFFFFF;
    padding: 7px 0 0px;
    border: 1px solid rgba(82, 92, 105, 0.11) !important;
    border-radius: 4px;
    margin-top: 2px; }
  .select2-container--custom-theme .select2-results__option {
    padding: 11px 9px 11px;
    border-bottom: 1px solid rgba(82, 92, 105, 0.11);
    transition-duration: .3s; }
    .select2-container--custom-theme .select2-results__option:hover {
      opacity: .7; }
    .select2-container--custom-theme .select2-results__option:last-child {
      border: none; }

@media screen and (max-width: 1040px) {
  .h1 {
    font-size: 30px; }
  .tabs-triggers {
    margin-top: 23px; }
  .online-donation {
    margin-top: 23px; }
  .online-donation__items {
    margin-top: 23px; } }

@media screen and (max-width: 820px) {
  .online-donation__item {
    width: calc(50% - 30px); } }

@media screen and (max-width: 640px) {
  .h1 {
    font-size: 25px; }
  .tabs-triggers {
    margin-top: 18px; }
    .tabs-triggers__item {
      margin-bottom: 17px; }
  .donations-accounts {
    margin-top: 23px; }
  .donations-accounts__cards {
    margin-left: 0;
    margin-bottom: -23px; }
  .donations-accounts__card {
    width: 100%;
    margin-left: 0;
    margin-bottom: 23px;
    padding: 20px; }
  .donations-accounts__card-info {
    margin-top: 15px; }
  .donations-row:not(:first-child) {
    margin-top: 15px; }
  .online-donation__items {
    margin-left: 0; }
  .online-donation__item {
    width: 100%;
    margin-left: 0; }
  .things-necessary-categories {
    padding: 20px; } }

@media screen and (max-width: 570px) {
  .questionnaire-volunteer-main {
    width: 100%; }
  .q-input-label {
    font-size: 13px;
    left: 0; }
  .q-input {
    padding-left: 0 !important; }
  .select2-container--custom-theme .select2-selection--single .select2-selection__rendered {
    padding-left: 0; } }

@media screen and (max-width: 460px) {
  .h1 {
    font-size: 22px; } }
